import './../scss/index.scss';
import { preloaderDestroy, afterPreloaderAnimation } from './preloader';
import { slider } from './slider';

window.addEventListener("load", () => {
    if (document.readyState === 'complete') {
        preloaderDestroy(); //turn off preloader
        afterPreloaderAnimation('.header-cards-cover', 'slide 1s cubic-bezier(0.12, 0.04, 0.58, 1)');        
        afterPreloaderAnimation('.header-cards-battons', 'slide-but 2s cubic-bezier(0.12, 0.04, 0.58, 1)');    
        
        slider('.header-cards-item', '.leftBtn', '.rightBtn', '.header-cards-cover');
        slider('.responce-content p', '.respLeftBtn', '.respRightBtn', '.responce-content');
    }

    let anchors =  document.querySelectorAll('a[href="#"]');//get all anchors from web site with attribute href=#
    let countAnchors = 0;//this is start counting all of anchors on web site
    anchors.forEach( item => { // sorting throught all of anchors arrey
        item.addEventListener('click', (item) => item.preventDefault()); // on other item adding click listener, and set for him preventDefault settings
        countAnchors++;//on other iteration adding one to counter
    });
    console.log("You have " + countAnchors + " with attribute #");//view resalts of sum the anchors on the website



// start of show hidden menu to mobile viewport
    let button = document.querySelector('.burger-menu-but');

    button.addEventListener('click', (elem) => {
        let menu = document.querySelector('.burger-menu-hidden');
        let socialHeight = document.querySelector('.social-nav').offsetHeight;
        let openMenu = menu.style.transform = "translateY(" + socialHeight + "px)";
        document.body.style.overflow = "hidden";

            if(openMenu){
                document.addEventListener('click', (event) => {
                    if (event.target.tagName === "SPAN" || event.target.tagName === "A"){
                        menu.style.transform = "translateY(-1000px)";
                        document.body.style.overflow = "visible";
                    }
                }, true);
                setTimeout(()=>{
                    menu.style.transform = "translateY(-1000px)";
                    document.body.style.overflow = "visible";
                }, 7000);
            }
    });
});

// start of show hidden menu to mobile viewport
