export let preloaderDestroy =  () => {
    let elementToDestroy = document.querySelector('.preloader');
    elementToDestroy.parentNode.removeChild(elementToDestroy);
};

export let afterPreloaderAnimation = (elemClass, animationStyle) => {
    let elem = document.querySelector(elemClass);
    elem.style.animation =  animationStyle;
};