export let slider = (items, lbtn, rbtn, viewPort, auto=false) => {

    //variables
    let cardsArchive = document.querySelectorAll(items),                             //get gallery cards
        leftBtn = document.querySelector(lbtn),                                      //get left button
        rightBtn = document.querySelector(rbtn),                                     //get right button
        viewPortWidth = document.querySelector(viewPort).offsetWidth,                //get slider container width
        counter = 1;                                                                 //set counter to 1


        if (leftBtn && rightBtn) {                                                   //checking if arguments have buttons     

            //right btn click listener
            rightBtn.addEventListener('click', () => {
                counter++;
                if (cardsArchive.length < counter) {
                    counter = 1;
                }
                cardsArchive.forEach( item => item.style.transform = "translateX(-" + ((counter-1) * viewPortWidth) + "px)");
            });

            //left btn click listener
            leftBtn.addEventListener('click', () => {
                counter--;
                if (counter < 1) {
                    counter = cardsArchive.length;
                }
                cardsArchive.forEach( item => item.style.transform = "translateX(-" + ((counter-1) * viewPortWidth) + "px)");
            });

        };

        let automaticSlider = () => {
            let timer = setInterval(()=>{
                counter++;
                if (counter > cardsArchive.length) {
                    counter = 1;
                };
                cardsArchive.forEach( (item) => {
                    item.style.transform = "translateX(-" + ((counter-1) * viewPortWidth) + "px)";
                });
            }, 1000);

            let hover = document.querySelector('.cards-wrap');

            hover.addEventListener("mouseenter", () => {
                clearInterval(timer);
            });
        };

        if (auto) {
            automaticSlider();
        };
};